import React from 'react';
import styled from 'styled-components';
import { graphql, PageProps } from 'gatsby';
import Card from '@components/Card';
import { defineCustomElements as deckDeckGoElement } from '@deckdeckgo/highlight-code/dist/loader';

import 'katex/dist/katex.min.css';

import {
  PostLayout,
  PostFileContent,
  PostMarkdownContent,
} from '../templates/post.styles';

deckDeckGoElement();

const ContentBox = styled.ul`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
`;

const AboutProfileImage = styled.img`
  width: 100%;
  aspect-ratio: 1/1;
  padding: 1.25rem;
`;

interface AboutPageProps {
  file: {
    children: Array<{
      html: string;
    }>;
  } | null;
}

function About({
  data,
  pageContext,
}: PageProps<AboutPageProps, PageContextType>) {
  return (
    <ContentBox>
      <Card maxWidth="33%">
        <AboutProfileImage src={pageContext.profileUrl} />
      </Card>
      {data.file && (
        <Card title="소개">
          <PostLayout>
            <PostFileContent>
              <PostMarkdownContent>
                <div
                  style={{
                    padding: '1.25rem',
                  }}
                  dangerouslySetInnerHTML={{
                    __html: data.file.children[0].html,
                  }}
                />
              </PostMarkdownContent>
            </PostFileContent>
          </PostLayout>
        </Card>
      )}
    </ContentBox>
  );
}

export const query = graphql`
  query {
    file(relativeDirectory: { eq: "" }, ext: { eq: ".md" }) {
      children {
        ... on MarkdownRemark {
          html
        }
      }
    }
  }
`;

export default About;
